import uniDrawer from "@/components/uni-drawer/uni-drawer.vue"
import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;
let {
	log
} = console;
export default {
	components: {
		uniDrawer
	},
	data() {
		return {
			kind: 0,
			active: -1,
			activeTab: 0,
			loading: true,
			firstId: '',
			secondId: '',
			page: 1,
			num: 10,
			firstCate: [],
			secondCate: [],
			list: [],
			config: [],
			cartware: [],
			arrObj:{},
			index:''
		};
	},
	methods: {
		toParent(e){
		
			let item = e.arrObj;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				item_id: item.id,
				attr_id: 0,
				item_num: e.val
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.postRequests('changeNum', params, res => { 
				let data = res.data;
				if (data.code == 200) {
					uni.showToast({
						title:'加入购物车成功',
						icon:'none',
						duration:2000
					})
					this.list[this.index].cart_num=e.val;
				} else if (data.code == 407 || data.code == 406) {
					uni.showToast({
						title:"购买数量不能超过活动数量",
						icon:'none',
						duration:2000
					})
				} else {
					uni.showToast({
						title:res.data.msg,
						icon:'none',
						duration:2000
					})
				}
			});
			this.$refs.popup.close();
		},
		mpItem() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let {
				firstId,
				secondId,
				page,
				num
			} = this;

			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign,
					firstId: firstId,
					secondId: secondId,
					page: 1,
					num: num
				},
				obj
			);
			rs.getRequest('mpItemList', params, res => {
				let data = res.data;
				if (data.code == 200) {
					if (!firstId) {
						firstId = data.data.firstCate[0].id;
					}
					if (firstId) {
						data.data.firstCate.map((e, index) => {
							if (firstId == e.id) {
								this.activeTab = index;
							}
						});
					}
					this.config = data.data;
					this.firstCate = data.data.firstCate;
					this.secondCate = data.data.secondCate;
					this.list = data.data.list;
					if (data.data.list.length < 10) {
						this.loading = false;
					} else {
						this.loading = true;
					}
				}
			});
		},
		// 切换一级分类
		changeFirst(index) {
			this.firstId = this.firstCate[index].id;
			this.secondId = "";
			this.kind = 0;
			getApp().globalData.classId = "";
			this.mpItem();
		},
		// 切换二级分类
		changeSecond(index) {
			this.secondId = this.secondCate[index].id;
			this.kind = index;
			this.mpItem();
		},
		openCart(item) {
			this.cartware = item;
			this.$refs.cart.open();
		},
		onClose() {
			this.$refs.cart.close();
		},
		// 显示键盘
		showKey(item,index) {
			
			this.arrObj=item;
			this.index=index;
			this.$refs.popup.open();
		},
		showDraw() {
			this.$refs.drawer.open()
		},
		selectSort(index) {
			this.active = index;
		},
		cancelSort() {
			this.active = -1;
			this.$refs.drawer.close();
		},
		deterSort() {
			if (this.active == -1) {
				uni.showToast({
					title: '请先选择分类',
					icon: 'none'

				})
				return;
			}
			this.firstId = this.firstCate[this.active].id;
			this.activeTab = this.active;
			this.secondId = "";
			this.kind = 0;
			this.mpItem();
			this.cancelSort();
		}
	},
	onShow() {
		let classId = getApp().globalData.classId;
		if (classId) {
			this.page = 1;
			this.firstId = classId
		}
		if (this.page == 1) {
			this.mpItem();
		}

	},
	onReachBottom() {
		var that = this;
		var timeStamp = Math.round(new Date().getTime() / 1000);
		var {
			num,
			page,
			list,
			secondId,
			firstId
		} = that;
		var obj = {
			appid: appid,
			timeStamp: timeStamp
		};
		var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		var data = {
			appid: appid,
			num: num,
			page: page + 1,
			firstId: firstId,
			secondId: secondId,
			timeStamp: timeStamp,
			sign: sign
		};
		rs.getRequests('mpItemList', data, res => {
			if (res.data.code == 200) {
				if (res.data.data.list.length != 0) {
					this.list.push(...res.data.data.list);
					this.page += 1;
					this.loading = true;
				} else {
					this.loading = false;
				}
			}
		})
	},
	onLoad(e) {

		uni.hideTabBar();
	}
};
